  let working = latex;

  try {
    working = applySafeMathFix(working);
  } catch (error) {
    console.warn('Failed to sanitize formula latex:', error?.message || error);
  }

  try {
    working = normalizeLatex(working);
  } catch (error) {
    console.warn('Failed to normalize formula latex:', error?.message || error);
  }

  return working;
