                          color: scheme.accentText,
                          borderColor: answers[currentIndex]
                            ? scheme.accent
                            : '#9ca3af',
                          opacity: answers[currentIndex] ? 1 : 0.6,
                          cursor: answers[currentIndex]
                            ? 'pointer'
                            : 'not-allowed',
